.align-center {
  text-align: center;
}

.ingredientsBtnBlock {
  background-color: #cf8f2e;
  padding: 20px;
  text-align: center;
}
.burgerIngredients {
  width: 600px;
  margin: auto;
  height: 500px;
  overflow: auto;
}
.burger-top {
  height: 20%;
  width: 80%;
  background: linear-gradient(#bc581e, #e27b36);
  border-radius: 50% 50% 0 0;
  box-shadow: inset -15px 0 #c15711;
  margin: 2% auto;
}
.burger-bottom {
  width: 80%;
  height: 13%;
  background: linear-gradient(#f08e4a, #e27b36);
  margin: 2% auto;
  box-shadow: inset -15px 0 #c15711;
  border-radius: 0 0 30px 30px;
}
.lettuce {
  width: 90%;
  height: 7%;
  background: linear-gradient(#228c1d, #91ce50);
  margin: 2% auto;
  border-radius: 20px;
}
.bacon {
  width: 80%;
  height: 3%;
  background: linear-gradient(#bf3813, #c45e38);
  margin: 2% auto;
}
.cheese {
  width: 90%;
  height: 5%;
  background: linear-gradient(#f4d004, #d6bb22);
  margin: 2% auto;
  border-radius: 20px;
}
.meat {
  width: 85%;
  height: 8%;
  background: linear-gradient(#7f3608, #702e05);
  margin: 2% auto;
  border-radius: 15px;
}

.order-btn {
  padding: 15px;
  border: none;
}
.order-btn:disabled {
  cursor: not-allowed;
}
.order-btn-active {
  background-color: #dad735;
}
.order-btn-active:hover {
  background-color: #a0db41;
}
